<template lang="pug">
  div#joinus
    div.container
      h1.joinus__title.section__title {{ serviceKo }} 
      p.section__subtitle
        | {{ stringUpper }}
        br
        | {{ stringBelow }}
        br
        span.joinus__title--copyEmail(
          @click="toast"
          v-clipboard:copy="mailTo"
          title="   "
        )   
          i.far.fa-copy

      div.global__cta
        a.global__cta--link(
          :href="`mailto:${mailTo}?subject= () `"
        )
          button.global__cta--btn.mailto(
            title="  ( )"
          )  

        router-link.global__cta--link(
          :to="'/sellerform'"
        )
          button.global__cta--btn.form(
            title=""
          ) 
</template>

<script>
import { globalVar } from '@/globalVar'

export default {
  name: 'joinus',

  data: () => ({
    mailTo: globalVar.mailTo,
    serviceKo: globalVar.serviceKo,
    stringUpper: '',
    stringBelow: '',
  }),

  methods: {
    toast () {
      this.$toasted.show('  ', {
        theme: "primary",
        position: "bottom-center",
        duration : 2000
      })
    },

  },

  mounted () {
    if (this.$route.path === '/') {
      this.stringUpper = this.serviceKo + '    .'
      this.stringBelow = '  ,   .'
    } else if (this.$route.path === '/seller') {
      this.stringUpper = '    ,'
      this.stringBelow = this.serviceKo + '   !'
    }
  },

}
</script>



