<template lang="pug">
  div#hero.sellerHero
    div.hero__dim
      div.container
        div.hero__text
          h1.hero__text--title
            |     
            br
            |    
          p.hero__text--subtitle {{ serviceEn }} 
        img.hero__text--image(
          src="/src/assets/dist/mockup.png"
        )
</template>

<script>
import { globalVar } from '@/globalVar'

export default {
  name: 'hero',

  data: () => ({
    serviceEn: globalVar.serviceEn,
  }),

}
</script>



