<template lang="pug">
  div#hero
    div.hero__dim
      div.container
        div.hero__text.center
          h1.hero__text--title
            |   
            span.hero__title--nbsp &nbsp;&nbsp;
            span.hero__br
            |  
          p.hero__text--subtitle {{ serviceEn }} 
          p.hero__text--spantext
            |    
            span.hero__title--nbsp &nbsp;
            span.hero__br
            |    
            br
            |   &amp;     

          DownloadBtn.dlbtn
</template>

<script>
import DownloadBtn from '@/components/misc/DownloadBtn'
import { globalVar } from '@/globalVar'

export default {
  name: 'hero',

  data: () => ({
    serviceEn: globalVar.serviceEn,
  }),

  components: {
    DownloadBtn,
  },

}
</script>



