<template lang="pug">
  div#download
    div.container
      h1.download__title.section__title   
      p.section__subtitle
        | {{ serviceKo }}    ,      .

      DownloadBtn

      div.download__mockup
        img.download__mockup--img(
          src="/src/assets/dist/screen.png"
        )
</template>

<script>
import DownloadBtn from '@/components/misc/DownloadBtn'
import { globalVar } from '@/globalVar'

export default {
  name: 'download',

  data: () => ({
      serviceKo: globalVar.serviceKo,
  }),

  components: {
    DownloadBtn,
  },

}
</script>



